<script>
import uniPopup from '@/components/uni-popup/uni-popup.vue'

export default {
  components: { uniPopup },
  data() {
    return {
      hallItems: [],
      currentHallItem: {
        name: '',
        hallName: '',
        price: '',
        description: '',
        status: 'ÂêØÁî®'
      },
      isEdit: false,
      editIndex: -1,
      loading: false,
      showHallPopup: false
    }
  },
  computed: {
    enabledCount() {
      return this.hallItems.filter(item => item.status === 'ÂêØÁî®').length
    },
    disabledCount() {
      return this.hallItems.filter(item => item.status !== 'ÂêØÁî®').length
    }
  },
  onLoad() {
    this.loadHallItems()
  },
  methods: {
    async loadHallItems() {
      try {
        this.loading = true
        console.log('ÂºÄÂßãÂä†ËΩΩÊÆøÂ†Ç‰æõÂìÅÊï∞ÊçÆ...')
        
        const result = await uniCloud.callFunction({ name: 'getHallItems' })
        console.log('ÊÆøÂ†Ç‰æõÂìÅÊï∞ÊçÆÂä†ËΩΩÁªìÊûú:', result)
        
        if (result.result && result.result.data) {
          this.hallItems = result.result.data
          console.log('ÊÆøÂ†Ç‰æõÂìÅÊï∞ÊçÆÂä†ËΩΩÊàêÂäüÔºåÂÖ±', this.hallItems.length, 'Êù°')
        } else {
          this.hallItems = []
          console.log('ÊÆøÂ†Ç‰æõÂìÅÊï∞ÊçÆ‰∏∫Á©∫')
        }
      } catch (error) {
        console.error('Âä†ËΩΩÊÆøÂ†Ç‰æõÂìÅÂ§±Ë¥•:', error)
        uni.showToast({ 
          title: 'Âä†ËΩΩÂ§±Ë¥•: ' + (error.message || 'Êú™Áü•ÈîôËØØ'), 
          icon: 'none',
          duration: 3000
        })
        this.hallItems = []
      } finally {
        this.loading = false
      }
    },
    showAddModal() {
      console.log('ÊâìÂºÄÊ∑ªÂä†‰æõÂìÅÂºπÁ™ó')
      this.isEdit = false
      this.editIndex = -1
      this.currentHallItem = { 
        name: '', 
        hallName: '', 
        price: '', 
        description: '',
        status: 'ÂêØÁî®'
      }
      this.showHallPopup = true
    },
    editHallItem(index) {
      this.isEdit = true
      this.editIndex = index
      this.currentHallItem = { ...this.hallItems[index] }
      this.showHallPopup = true
    },
    async deleteHallItem(index) {
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: `Á°ÆÂÆöË¶ÅÂà†Èô§"${this.hallItems[index].name || 'Êú™ÂëΩÂêç'}"‰æõÂìÅÂêóÔºüÊ≠§Êìç‰Ωú‰∏çÂèØÊÅ¢Â§ç„ÄÇ`,
        confirmText: 'Âà†Èô§',
        confirmColor: '#ff3b30',
        success: async (res) => {
          if (res.confirm) {
            try {
              uni.showLoading({ title: 'Âà†Èô§‰∏≠...' })
              await uniCloud.callFunction({
                name: 'deleteHallItem',
                data: { id: this.hallItems[index]._id }
              })
              this.hallItems.splice(index, 1)
              uni.hideLoading()
              uni.showToast({ 
                title: 'Âà†Èô§ÊàêÂäü', 
                icon: 'success',
                duration: 2000
              })
            } catch (error) {
              uni.hideLoading()
              uni.showToast({ 
                title: 'Âà†Èô§Â§±Ë¥•: ' + (error.message || 'Êú™Áü•ÈîôËØØ'), 
                icon: 'none',
                duration: 3000
              })
              console.error('Âà†Èô§‰æõÂìÅÂ§±Ë¥•:', error)
            }
          }
        }
      })
    },
    async saveHallItem() {
      if (!this.currentHallItem.name) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•‰æõÂìÅÂêçÁß∞', icon: 'none' })
        return
      }
      if (!this.currentHallItem.hallName) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÊÆøÂ†ÇÂêçÁß∞', icon: 'none' })
        return
      }
      if (!this.currentHallItem.price) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÂäüÂæ∑ÈáëÈáëÈ¢ù', icon: 'none' })
        return
      }
      
      try {
        this.loading = true
        console.log('‰øùÂ≠òÊÆøÂ†Ç‰æõÂìÅÊï∞ÊçÆ:', this.currentHallItem)
        
        if (this.isEdit) {
          console.log('Êõ¥Êñ∞‰æõÂìÅÔºåID:', this.hallItems[this.editIndex]._id)
          const updateResult = await uniCloud.callFunction({
            name: 'updateHallItem',
            data: { id: this.hallItems[this.editIndex]._id, item: this.currentHallItem }
          })
          console.log('Êõ¥Êñ∞ÁªìÊûú:', updateResult)
          this.hallItems[this.editIndex] = { ...this.currentHallItem }
        } else {
          console.log('Ê∑ªÂä†Êñ∞‰æõÂìÅ')
          const result = await uniCloud.callFunction({
            name: 'addHallItem',
            data: { item: this.currentHallItem }
          })
          console.log('Ê∑ªÂä†ÁªìÊûú:', result)
          if (result.result && result.result.data) {
            this.hallItems.push(result.result.data)
          } else {
            throw new Error('Ê∑ªÂä†Â§±Ë¥•ÔºöËøîÂõûÊï∞ÊçÆÊ†ºÂºèÈîôËØØ')
          }
        }
        this.closePopup()
        uni.showToast({ title: this.isEdit ? 'Êõ¥Êñ∞ÊàêÂäü' : 'Ê∑ªÂä†ÊàêÂäü', icon: 'success' })
      } catch (error) {
        console.error('‰øùÂ≠ò‰æõÂìÅÂ§±Ë¥•:', error)
        uni.showToast({ 
          title: '‰øùÂ≠òÂ§±Ë¥•: ' + (error.message || 'Êú™Áü•ÈîôËØØ'), 
          icon: 'none',
          duration: 3000
        })
      } finally {
        this.loading = false
      }
    },
    closePopup() {
      console.log('ÂÖ≥Èó≠ÂºπÁ™ó')
      this.showHallPopup = false
      this.currentHallItem = { 
        name: '', 
        hallName: '', 
        price: '', 
        description: '',
        status: 'ÂêØÁî®'
      }
    },
    async toggleHallItemStatus(item, e) {
      const newStatus = e.detail.value ? 'ÂêØÁî®' : 'Á¶ÅÁî®'
      const oldStatus = item.status
      
      try {
        console.log('ÂàáÊç¢‰æõÂìÅÁä∂ÊÄÅ:', item._id, oldStatus, '->', newStatus)
        
        if (!item._id) {
          throw new Error('‰æõÂìÅID‰∏çÂ≠òÂú®')
        }
        
        item.status = newStatus
        
        const result = await uniCloud.callFunction({ 
          name: 'updateHallItem', 
          data: { 
            id: item._id, 
            item: { status: newStatus } 
          } 
        })
        
        console.log('Áä∂ÊÄÅÊõ¥Êñ∞ÁªìÊûú:', result)
        
        uni.showToast({ 
          title: `${newStatus}ÊàêÂäü`, 
          icon: 'success',
          duration: 1500
        })
      } catch (error) {
        item.status = oldStatus
        console.error('Áä∂ÊÄÅÊõ¥Êñ∞Â§±Ë¥•:', error)
        uni.showToast({ 
          title: 'Áä∂ÊÄÅÊõ¥Êñ∞Â§±Ë¥•: ' + (error.message || 'Êú™Áü•ÈîôËØØ'), 
          icon: 'none',
          duration: 3000
        })
      }
    }
  }
}
</script>

